function injectFonts(typography) {
  var fontsStr = getFontsStr(typography);

  if (fontsStr) {
    var link = getFontsLink(fontsStr);
    injectLink(link);
  } else {
    // dunno if this is ideal behavior
    throw new Error('no fonts str');
  }
}

function injectLink(link) {
  var typoElt = document.getElementById("typography.js");

  if (typoElt) {
    typoElt.insertAdjacentHTML('afterend', link);
  } else {
    // i don't feel like handling this case right now
    throw new Error('no typo elt');
  }
}

function getFontsStr(typography) {
  var fontsStr = "";

  if (typography.options.googleFonts) {
    var fonts = typography.options.googleFonts.map(function (font) {
      var str = "";
      str += font.name.split(" ").join("+");
      str += ":";
      str += font.styles.join(",");
      return str;
    });
    fontsStr = fonts.join("|");
  }

  return fontsStr;
}

function getFontsLink(str) {
  var link = "<link href=\"".concat(str, "\" rel=\"stylesheet\" type=\"text/css\" />");
  return link;
}

export default injectFonts;